import React, { useState } from "react";


const AddProperty = ({  instance }) => {
	const [formData, setFormData] = useState({
		address: "",
		rent: "",
		name: "",
	});
	const [spendPromise, setSpendPromise] = useState(null);

	let aci=[
		{
		  "namespace": {
			"name": "ListInternal",
			"typedefs": []
		  }
		},
		{
		  "namespace": {
			"name": "List",
			"typedefs": []
		  }
		},
		{
		  "namespace": {
			"name": "String",
			"typedefs": []
		  }
		},
		{
		  "contract": {
			"functions": [
			  {
				"arguments": [],
				"name": "init",
				"payable": false,
				"returns": "SmartRealEstate.state",
				"stateful": false
			  },
			  {
				"arguments": [
				  {
					"name": "name",
					"type": "string"
				  },
				  {
					"name": "price",
					"type": "int"
				  },
				  {
					"name": "property_address",
					"type": "string"
				  }
				],
				"name": "add_property",
				"payable": false,
				"returns": {
				  "tuple": []
				},
				"stateful": true
			  },
			  {
				"arguments": [],
				"name": "getproperty",
				"payable": false,
				"returns": {
				  "option": [
					"SmartRealEstate.rent"
				  ]
				},
				"stateful": false
			  },
			  {
				"arguments": [
				  {
					"name": "owner_address",
					"type": "address"
				  },
				  {
					"name": "name",
					"type": "string"
				  }
				],
				"name": "pay_rent",
				"payable": true,
				"returns": {
				  "tuple": []
				},
				"stateful": true
			  },
			  {
				"arguments": [],
				"name": "get_payment_status_byowner",
				"payable": false,
				"returns": {
				  "option": [
					"bool"
				  ]
				},
				"stateful": false
			  },
			  {
				"arguments": [
				  {
					"name": "owner_address",
					"type": "address"
				  },
				  {
					"name": "name",
					"type": "string"
				  }
				],
				"name": "get_tenant_address",
				"payable": false,
				"returns": {
				  "option": [
					"address"
				  ]
				},
				"stateful": false
			  },
			  {
				"arguments": [],
				"name": "get_all_properties",
				"payable": false,
				"returns": "SmartRealEstate.state",
				"stateful": false
			  }
			],
			"kind": "contract_main",
			"name": "SmartRealEstate",
			"payable": false,
			"state": {
			  "map": [
				"address",
				"SmartRealEstate.rent"
			  ]
			},
			"typedefs": [
			  {
				"name": "rent",
				"typedef": {
				  "record": [
					{
					  "name": "owner_address",
					  "type": "address"
					},
					{
					  "name": "tenant_address",
					  "type": "address"
					},
					{
					  "name": "price",
					  "type": "int"
					},
					{
					  "name": "is_paid",
					  "type": "bool"
					},
					{
					  "name": "name",
					  "type": "string"
					},
					{
					  "name": "property_address",
					  "type": "string"
					}
				  ]
				},
				"vars": []
			  }
			]
		  }
		}
	  ];

	  let bytecode="cb_"


	const handleInputChange = (e) => {
		const { name, value } = e.target;
		setFormData((prevData) => ({
			...prevData,
			[name]: name === "rent" ? (value ? parseInt(value, 10) : "") : value,
		}));
	};

	const handleSubmit = async (e) => {
		e.preventDefault();
		console.log(formData.name);
		console.log(formData.rent);
		console.log(formData.address);

		console.log(instance);
		const contract = await instance.initializeContract({ aci, bytecode, address: "ct_mzdNPHxsu9cRhwMLqebBWQasZ6ctsH6wUPaif9MauKCydFeMk" })
		const options1 = {
			amount: 0,
			callData: "",
			fee: null,
			gas: null,
			gasPrice: 1000000000,
		};
		const args = [formData.name,formData.rent,formData.address];
		const options = Object.fromEntries(
			Object.entries(options1).filter(([, v]) => v != null),
		);
  
		contract
			?.$call("add_property",args, options)
			.then((result) => {
				console.log(result);
				setSpendPromise(result.hash)
				
				
				console.log(result);
				
				
				
  
			});
	};

	return (
		<div className="h-screen rounded-lg">
			<form
				onSubmit={handleSubmit}
				className="max-w-[400px] w-full bg-gray-900 p-8 px-8 border 2px white"
			>
				<h2 className="text-4xl dark:text-white font-bold text-center">
					Add Property
				</h2>
				<div className="flex flex-col text-gray-400 py-2">
					<label>Name</label>
					<input
						className="p-2 rounded-lg bg-gray-700 mt-2 focus:border-blue-500 focus:bg-gray-800 focus:outline-none"
						type="text"
						name="name"
						value={formData.name}
						onChange={handleInputChange}
						placeholder="Enter name"
					/>
				</div>
				<div className="flex flex-col text-gray-400 py-2">
					<label>Rent</label>
					<input
						className="p-2 rounded-lg bg-gray-700 mt-2 focus:border-blue-500 focus:bg-gray-800 focus:outline-none"
						type="number"
						name="rent"
						value={formData.rent}
						onChange={handleInputChange}
						required
						placeholder="Enter Rent "
					/>
				</div>
				<div className="flex flex-col text-gray-400 py-2">
					<label>Address</label>
					<input
						className="rounded-lg bg-gray-700 mt-2 p-2 focus:border-blue-500 focus:bg-gray-800 focus:outline-none"
						type="text"
						name="address"
						value={formData.address}
						onChange={handleInputChange}
						required
						placeholder="Enter the address"
					/>
				</div>

				<button
					type="submit"
					className="w-full my-5 py-2 bg-teal-500 shadow-lg shadow-teal-500/50 hover:shadow-teal-500/40 text-white font-semibold rounded-lg"
				>
					Add this Property
				</button>
				{spendPromise && (
        <div className="mt-4">
          <div className="font-bold text-lg mb-2">Spend result</div>
          <p className="text-gray-700">{spendPromise}</p>
        </div>
      )}
			</form>
		</div>
	);
};

export default AddProperty;
